import Paper from '@mui/material/Paper';
import { fadeIn } from 'react-animations';
import { StyleSheet, css } from 'aphrodite';
import { useState, useEffect } from 'react';

const styles = StyleSheet.create({
  fadeIn: {
    animationName: fadeIn,
    animationDuration: '1.5s',
    animationDelay: '0s'
  },
  hide: {
    display: "none"
  }
});


export default function WhatChanged(props){
    const [Dialogue1, setDialogue1] = useState(<Paper className={css(styles.hide)} elevation={2}>Please hand me your passport and I-20.</Paper>);
    const [Dialogue2, setDialogue2]= useState(<Paper className={css(styles.hide)} elevation={2}>Thank you.</Paper>);

    useEffect(() => {
        const timer = setTimeout(() => {
            setDialogue1(<Paper className={css(styles.fadeIn)} elevation={2}>Alright, I see that you have applied once before.</Paper>);
        }, 2000);
        return () => clearTimeout(timer);
      }, []);

    useEffect(()=>{
      const timer = setTimeout(() => {
        setDialogue2(<Paper className={css(styles.fadeIn)} elevation={2}><strong>So what has changed since last time?</strong></Paper>);
    }, 3500);
    return () => clearTimeout(timer);
  }, []);

    return (<>
        {Dialogue1}
        {Dialogue2}
        </>
    );
}