import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faForward } from "@fortawesome/free-solid-svg-icons";
import { tada } from 'react-animations';
import { StyleSheet, css } from 'aphrodite';
import { useState, useEffect } from 'react';

const styles = StyleSheet.create({
  tada: {
    animationName: tada,
    animationDuration: '1.0s',
    animationDelay: '0s'
  },
  hide: {
    display: "none"
  }
});


export default function IggyInitialDialogue(props){
    const [Dialogue1, setDialogue1] = useState(<Paper className={css(styles.hide)} elevation={2}>WOW you found me!</Paper>);
    const [Dialogue2, setDialogue2]= useState(<Paper className={css(styles.hide)} elevation={2}>BABE!</Paper>);
    // const [show, setShow] = useState(true);
    // const toggleShow = () => {
    //     setShow(!show);
    // };
    useEffect(() => {
        const timer = setTimeout(() => {
            setDialogue1(<Paper className={css(styles.tada)} elevation={2}><strong style={{color: 'red'}}>Wow</strong> you found me!</Paper>);
            // console.log('Classnames changed after 3 seconds!')
        }, 2000);
        return () => clearTimeout(timer);
      }, []);

    useEffect(() => {
    const timer = setTimeout(() => {
        setDialogue2(<Paper className={css(styles.tada)} elevation={2}>BABE!</Paper>);
        // console.log('Classnames changed after 4 seconds!')
    }, 4000);
    return () => clearTimeout(timer);
    }, []);

    return (<>
        {Dialogue1}
        {Dialogue2}
        {/* <Button className={show ? "": css(styles.hide)} onClick={toggleShow}><FontAwesomeIcon icon={faForward} style={{color: "#183153", width: "20px"}}></FontAwesomeIcon></Button> */}
        <Button onClick={props.next}><FontAwesomeIcon icon={faForward} style={{color: "#183153", width: "20px"}}></FontAwesomeIcon></Button>

        <></>
        </>
    );
}