import Paper from '@mui/material/Paper';
import { bounceInUp } from 'react-animations';
import { StyleSheet, css } from 'aphrodite';
import { useState, useEffect } from 'react';

const styles = StyleSheet.create({
    bounceInUp: {
    animationName: bounceInUp,
    animationDuration: '0.35s',
    animationDelay: '0s'
  },
  hide: {
    display: "none"
  }
});


export default function Response(props){
    const [Dialogue1, setDialogue1] = useState(<Paper className={css(styles.hide)} elevation={1}>{props.text}</Paper>);

    useEffect(() => {
        const timer = setTimeout(() => {
            setDialogue1(<Paper className={css(styles.bounceInUp)} elevation={1}><span style={{color:'#696880'}}>{props.text}</span></Paper>);
            // console.log('Classnames changed after 1 second!')
        }, 1000);
        return () => clearTimeout(timer);
      }, []);

    return (<>
        {Dialogue1}
        </>
    );
}