import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faForward } from "@fortawesome/free-solid-svg-icons";
import { fadeIn, bounceInDown } from 'react-animations';
import { StyleSheet, css } from 'aphrodite';
import { useState, useEffect } from 'react';
import seal from '../imgs/seal.png';

const styles = StyleSheet.create({
fadeIn: {
    animationName: fadeIn,
    animationDuration: '2s',
    animationDelay: '0s'
    },
  bounceInDown: {
    animationName: bounceInDown,
    animationDuration: '2s',
    animationDelay: '0s'
  },
  hide: {
    display: "none"
  }
});


export default function Iggy2(props){
    const [Dialogue1, setDialogue1] = useState(<Paper className={css(styles.hide)} elevation={2}>shh... let's not attract unwanted attention</Paper>);
    const [Dialogue2, setDialogue2]= useState(<Paper className={css(styles.hide)} elevation={2}>Deep breathes. <strong>Deeep</strong> breathes</Paper>);
    const [Dialogue3, setDialogue3]= useState(<Paper className={css(styles.hide)} elevation={2}></Paper>);
    const [Dialogue4, setDialogue4]= useState(<Paper className={css(styles.hide)} elevation={2}></Paper>);

    const record = () => {
        return(
        <Box className={css(styles.bounceInDown)}
        sx={{
          display: 'flex',
          flexDirection: 'column',
          gap: '0.5em',
          flexWrap: 'wrap',
          p: 1.5,
          m: 1,
          bgcolor: '#F5F5F5',
          // maxHeight: '700em'
        }}
      >
        <Paper elevation={1}>
            <div className='recordQuestion'><em>Why do you want to study in the US?</em></div>
            <div className='recordAnswer'>Because <span style={{color:'#E3242B'}}>Amber is a stellar person</span> and she deserves nothing but the best education.</div>
            <br />
            <div className='recordQuestion'><em>What has changed since last time?</em></div>
            <div className='recordAnswer'>She has <span style={{color:'#E3242B'}}>all the proof in the world</span> to show that she will thrive as a university student!</div>
            <br />
            <div className='recordQuestion'><em>Why do you want to study Business?</em></div>
            <div className='recordAnswer'>Because she will eventually become a world class <span style={{color:'#E3242B'}}>CEO</span></div>
            <br />
            <div className='recordQuestion'><em>Will you transfer after 2 years?</em></div>
            <div className='recordAnswer'>Yes she will move on to greater things, <span style={{color:'#E3242B'}}>greatness never ends.</span></div>
            <br />
            <div className='recordQuestion'><em>Who will fund your studies?</em></div>
            <div className='recordAnswer'>Her <span style={{color:'#E3242B'}}>little piglets</span> and beloved minions</div>
            <br />
            <div className='recordQuestion'><em>What are your plans after studying abroad?</em></div>
            <div className='recordAnswer'><span style={{color:'#E3242B'}}>To start her own venture and begin conquering the world!</span></div>
            <img src={seal} alt="Appoval Seal"></img>
        </Paper>
      </Box>);
    }

    useEffect(() => {
        const timer = setTimeout(() => {
            setDialogue1(<Paper className={css(styles.fadeIn)} elevation={2}>Also, <strong style={{color:'#E3242B'}}>HAPPY BIRTHDAY!!</strong></Paper>);
            // console.log('Classnames changed after 3 seconds!');
        }, 2000);
        return () => clearTimeout(timer);
      }, []);

    useEffect(() => {
    const timer = setTimeout(() => {
        setDialogue2(<Paper className={css(styles.fadeIn)} elevation={2}>Here is your present: </Paper>);
        // console.log('Classnames changed after 4 seconds!');
    }, 4500);
    return () => clearTimeout(timer);
    }, []);

    useEffect(() => {
      const timer = setTimeout(() => {
          setDialogue3(record);
      }, 7000);
      return () => clearTimeout(timer);
      }, []);

    useEffect(() => {
      const timer = setTimeout(() => {
          setDialogue4(<Paper style={{marginTop:"10px"}}>End of Emulation, Press Refresh to Play Again</Paper>);
      }, 9000);
      return () => clearTimeout(timer);
      }, []);



    return (<>
        {Dialogue1}
        {Dialogue2}
        {Dialogue3}
        {Dialogue4}
        </>
    );
}