import Paper from '@mui/material/Paper';
import { fadeIn } from 'react-animations';
import { StyleSheet, css } from 'aphrodite';
import { useState, useEffect } from 'react';

const styles = StyleSheet.create({
  fadeIn: {
    animationName: fadeIn,
    animationDuration: '1.5s',
    animationDelay: '0s'
  },
  hide: {
    display: "none"
  }
});


export default function WillTransfer(props){
    const [Dialogue1, setDialogue1] = useState(<Paper className={css(styles.hide)} elevation={2}>Please hand me your passport and I-20.</Paper>);

    useEffect(() => {
        const timer = setTimeout(() => {
            setDialogue1(<Paper className={css(styles.fadeIn)} elevation={2}><strong>Will you transfer after 2 years?</strong></Paper>);
            // console.log('Classnames changed after 1 seconds!')
        }, 1000);
        return () => clearTimeout(timer);
      }, []);

    return (<>
        {Dialogue1}
        </>
    );
}