import React from "react";
import "../Popup.css";

export const Popup = ({ text, closePopup }) => {  
    return (
        <div className="popup-container">
            <div className="popup-body">
            <div className="popup-btn"><button id="popupClose" onClick={closePopup}>X</button> </div>
                <p>{text}
                </p>
            </div>
        </div>);
    };