import logo from './logo.svg';
import './App.css';
import { useState, useRef, useEffect } from 'react';
import Button from '@mui/material/Button';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircleQuestion } from "@fortawesome/free-solid-svg-icons";
import {faRotateRight} from "@fortawesome/free-solid-svg-icons";
import officerfemale from './imgs/officerFemale.png';
import officermale from './imgs/officerMale.png';
import officeriggy from './imgs/officerIggy.png';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import InitialDialogue from './components/InitialDialogue';
import IggyInitialDialogue from './components/IggyInitialDialog';
import Iggy2 from './components/Iggy2';
import Response from './components/Response';
import TwoDiaReact from './components/TwoDiaReact';
import WhyAmerica from './components/WhyAmerica';
import WhatChanged from './components/WhatChanged';
import WhyBusiness from './components/WhyBusiness';
import WillTransfer from './components/WillTransfer';
import WhoFund from './components/WhoFund';
import PlansAfter from './components/PlansAfter';
import Last1 from './components/Last1';
import Last2 from './components/Last2';
import { fadeIn, bounce } from 'react-animations';
import { StyleSheet, css } from 'aphrodite';
import { Popup } from './components/Popup';

const styles = StyleSheet.create({
  fadeIn: {
    animationName: fadeIn,
    animationDuration: '1.5s'
  },
  bounce: {
    animationName: bounce,
    animationDuration: '1s',
  },
  hide: {
    display: 'none'
  }
});

function App() {
  const [currentDiag, setCurrentDiag] = useState(<InitialDialogue />);
  const [response, setResponse] = useState('');
  const [submittedResponse, setSubmittedResponse] = useState([]);
  const [officer, setOfficer] = useState(null);
  const [currentRes, setCurrentRes] = useState(<Response text=''/>);
  const responseRef = useRef("");
  const [disabled, setDisabled] = useState(false);
  const [open, setOpen] = useState(false);

  const last2 = <Last2 responseList={submittedResponse}/>;
  const last1 = <Last1 next={()=>{setCurrentDiag(last2)}} />;
  const plansAfter = <PlansAfter next = {()=>{setCurrentDiag(last1)}} />;
  const whoFund = <WhoFund next= {()=> {setCurrentDiag(plansAfter)}} />;
  const willTransfer = <WillTransfer next = {()=>{setCurrentDiag(whoFund)}} />;
  const whyBusiness = <WhyBusiness next = {()=>{setCurrentDiag(willTransfer)}} />;
  const whatChanged = <WhatChanged next = {()=> {setCurrentDiag(whyBusiness)}} />;
  const why = <WhyAmerica next = {() => {setCurrentDiag(whatChanged)}} />;
  const initialD = <InitialDialogue next= {()=> {setCurrentDiag(why)}} />;

  useEffect(()=>{
    const randomNumber = Math.random();
    setOfficer(randomNumber);
    if(randomNumber < 0.375) {
      setCurrentDiag(initialD);
    }
    else if(randomNumber < 0.75) {
      setCurrentDiag(initialD);
    }
    else {
      setCurrentDiag(<IggyInitialDialogue next={()=>{setCurrentDiag(<Iggy2 next={()=>{setCurrentDiag("")}}/>);}} />);
    }
  }, []);

  useEffect(() => {
    // console.log(currentDiag);
    // console.log(currentDiag.type.prototype.constructor.name);
    // console.log(InitialDialogue.type);
    if (currentDiag.type.prototype.constructor.name === "InitialDialogue" ||
    currentDiag.type.prototype.constructor.name === "IggyInitialDialogue" ||
    currentDiag.type.prototype.constructor.name === "Iggy2" ||
    currentDiag.type.prototype.constructor.name === "TwoDiaReact" ||
    currentDiag.type.prototype.constructor.name === "Last1" ||
    currentDiag.type.prototype.constructor.name === "Last2") {
      console.log("comments disabled!");
      setDisabled(true);
    } else {
      setDisabled(false);
    }
  }, [currentDiag]);

  function handleResponse() {
    const resObj = "";
    setCurrentRes(resObj);
    const totalResponse = submittedResponse;
    totalResponse.push(responseRef.current.value);
    // console.log(totalResponse);
    setSubmittedResponse(totalResponse);
    const birthdayRegex = /birthday|brthday|bday|birthdy|birtday|birth day/ig;
    const interestRegex = /business|mccc|community college|mba|company|jenny|affadavits|affadavit|certificate|deposits/ig;
    const offensiveRegex = /fuck|no|never|hell|hate|die|why|shit|stupid|dumb/ig;
    const interestFound = responseRef.current.value.match(interestRegex);
    const offenseFound = responseRef.current.value.match(offensiveRegex);
    const birthdayFound = responseRef.current.value.match(birthdayRegex);
    if (birthdayFound){
      const prevNext = currentDiag.props.next;
      const reaction = <TwoDiaReact text1="Oh it's your birthday!" text2="Happy Birthday Darling!" next={() => {prevNext()}}/>;
      setCurrentDiag(reaction);
    }
    else if(offenseFound){
      const prevNext = currentDiag.props.next;
      // console.log(prevNext);
      const reaction = <TwoDiaReact text1="Interesting answer." text2="Alight then!" next={() => {prevNext()}}/>;
      setCurrentDiag(reaction);
    }
    else if (interestFound){
      const prevNext = currentDiag.props.next;
      const topic = interestFound[0];
      const reaction = <TwoDiaReact text1={topic + " huh..."} text2="Great, that definately helps." next={() => {prevNext()}}/>;
      setCurrentDiag(reaction);
    }
    else {
      const prevNext = currentDiag.props.next;
      const reaction = <TwoDiaReact text1="Okay sounds good." text2="Moving on..." next={() => {prevNext()}}/>;
      setCurrentDiag(reaction);
    }
  }

  // useEffect(() => {
  //   setCurrentRes(<Response text={responseRef.current.value} />)
  // }, [currentRes]);
  var image = null;
  if(officer < 0.375) {
    image = officerfemale;
  }
  else if(officer < 0.75) {
    image = officermale;
  }
  else {
    image= officeriggy;
  }

  const submitForm = (e) => {
    e.preventDefault();
    const result = <Response text={'> ' + responseRef.current.value} />;
    setCurrentRes(result);
    setResponse(responseRef.current.value);
  }

  useEffect(()=>{
    console.log("response changed!");
  }, [response])

  return (
    <div className="App">
      <h1>Visa Interview Emulator</h1>
      
      <Box className="menuButtons"
      sx={{
        p: 1,
        m: 1,
        color: (theme) => (theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800'),
        border: '1px solid',
        borderColor: (theme) =>
          theme.palette.mode === 'dark' ? 'grey.800' : 'grey.300',
        borderRadius: 2,
        margin: "0px 60px 0 60px"
      }}>
        <Button onClick={()=> {setCurrentRes(<Response text=''/>); setSubmittedResponse([]); window.location.reload(false);}} style={{fontSize:"35px", width: "20px", marginRight: "60px"}}><FontAwesomeIcon icon={faRotateRight} style={{color: "#183153", width: "20px"}} /></Button>
        <Button onClick={() => setOpen(true)} style={{width: "20px"}}><FontAwesomeIcon icon={faCircleQuestion} style={{fontSize:"35px", color: "#183153", width: "20px"}} /></Button>
        {open ? <Popup text="Try saying whatever you want to different Visa Officers. You might even encounter a secret officer by refreshing the app." closePopup={() => setOpen(false)} /> : null}
      </Box>

      <Box className="officerWindow"
      sx={{
        p: 1,
        m: 1,
        color: (theme) => (theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800'),
        // border: '0.5px solid grey',
      }}>
        <img className= {css(styles.fadeIn)} src={image} alt="Officer" style = {{maxHeight: '150px'}}/>
      </Box>

      <Box className='dialogue'
        sx={{
          display: 'flex',
          flexDirection: 'column',
          gap: '0.5em',
          flexWrap: 'wrap',
          p: 1.5,
          m: 1,
          bgcolor: '#C2F8FF',
          // maxHeight: '700em'
        }}
      >
      {currentRes}
      {currentDiag}
      </Box>

      <Box className='dialogue'
      sx={{
        display: 'flex',
        m: 2,
        justifyContent: 'center'
        }}
      >

        <form onSubmit={submitForm}
        style={{
          display: 'flex',
          alignItems: 'stretch',
          gap: '10%'}}>
          <TextField
          id="outlined-textarea"
          label="Response"
          placeholder="Type your response"
          multiline
          inputRef={responseRef}
          onChange={()=>{setResponse(responseRef.current.value)}}
          // error={responseRef.current.value === ""}
          // helperText={responseRef.current.value === "" ? 'Empty field!' : ' '}
          />
          <Button onClick={()=> handleResponse()} color="primary" type="submit" variant="contained" disabled={(disabled || response === "") ? true:false}>Submit</Button>
        </form>

      </Box>
    </div>
  );
}

export default App;
