import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faForward } from "@fortawesome/free-solid-svg-icons";
import { fadeIn } from 'react-animations';
import { StyleSheet, css } from 'aphrodite';
import { useState, useEffect } from 'react';

const styles = StyleSheet.create({
fadeIn: {
    animationName: fadeIn,
    animationDuration: '1.5s',
    animationDelay: '0s'
    },
  hide: {
    display: "none"
  }
});


export default function Last1(props){
    const [Dialogue1, setDialogue1] = useState(<Paper className={css(styles.hide)} elevation={2}>shh... let's not attract unwanted attention</Paper>);
    const [Dialogue2, setDialogue2]= useState(<Paper className={css(styles.hide)} elevation={2}>Deep breathes. <strong>Deeep</strong> breathes</Paper>);
    // const [show, setShow] = useState(true);
    // const toggleShow = () => {
    //     setShow(!show);
    // };
    useEffect(() => {
        const timer = setTimeout(() => {
            setDialogue1(<Paper className={css(styles.fadeIn)} elevation={2}>Okay, based on all the info I have here...</Paper>);
            // console.log('Classnames changed after 3 seconds!');
        }, 2000);
        return () => clearTimeout(timer);
      }, []);

    useEffect(() => {
    const timer = setTimeout(() => {
        setDialogue2(<Paper className={css(styles.fadeIn)} elevation={2}>Congratuations! <strong style={{color:'red'}}>YOU HAVE PASSED!</strong></Paper>);
        // console.log('Classnames changed after 4 seconds!');
    }, 4000);
    return () => clearTimeout(timer);
    }, []);

    return (<>
        {Dialogue1}
        {Dialogue2}
        <Button onClick={props.next}><FontAwesomeIcon icon={faForward} style={{color: "#183153", width: "20px"}}></FontAwesomeIcon></Button>
        </>
    );
}